import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { Alert } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { RootState, AppDispatch } from '@store/Store'; //   .
import { signinUser } from '@store/slices/AuthSlice'; // authSlice signinUser  .

function Copyright(props: any) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright  '}
      <Link color="inherit" href="/">
        ict-project
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

// TODO remove, this demo shouldn't need to reset the theme.
const defaultTheme = createTheme();

export default function SignIn() {
  const [alertMessage, setAlertMessage] = React.useState<string | null>(null);
  const [alertSeverity, setAlertSeverity] = React.useState<'success' | 'error'>('success');

  const dispatch = useDispatch<AppDispatch>();
  const authState = useSelector((state: RootState) => state.auth);

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    const email = data.get('email') as string;
    const password = data.get('password') as string;

    const passwordRegex = /^(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])(?=.*[@#$%^&+=!])(?=\S+$).{8,40}$/;
    if (!passwordRegex.test(password)) {
      setAlertMessage('  8,  40, , ,     .');
      setAlertSeverity('error');
      return;
    }

    try {
      // Redux  
      await dispatch(signinUser({ email, password }));

      if (authState.status === 'succeeded') {
        setAlertMessage('     .');
        setAlertSeverity('success');
      } else if (authState.status === 'failed') {
        setAlertMessage(' : ' + authState.error);
        setAlertSeverity('error');
      }
    } catch (error) {
      console.error(' :', error);
      setAlertMessage('     ');
      setAlertSeverity('error');
    }
  };

  React.useEffect(() => {
    //  dispatch         useEffect
    if (authState.status === 'succeeded') {
      setAlertMessage(' ');
      setAlertSeverity('success');
    } else if (authState.status === 'failed') {
      setAlertMessage(' : ' + authState.error);
      setAlertSeverity('error');
    }
  }, [authState.status, authState.error]);

  return (
    <ThemeProvider theme={defaultTheme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            
          </Typography>

          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label=" "
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label=""
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <FormControlLabel control={<Checkbox value="remember" color="primary" />} label=" " />
            <Button type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
              
            </Button>
            {alertMessage && (
              <Alert severity={alertSeverity} sx={{ mt: 2 }}>
                {alertMessage}
              </Alert>
            )}
            <Grid container>
              <Grid item xs>
                <Link href="#" variant="body2">
                   
                </Link>
              </Grid>
              <Grid item>
                <Link href="/signup" variant="body2">
                  {'  ?  '}
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>
        <Copyright sx={{ mt: 8, mb: 4 }} />
      </Container>
    </ThemeProvider>
  );
}
